et margin here because it breaks `size` in Chrome. We also\n    // don't use `!important` on `size` as it breaks in Chrome.\n    @page {\n      size: $print-page-size;\n    }\n    body {\n      min-width: $print-body-min-width !important;\n    }\n    .container {\n      min-width: $print-body-min-width !important;\n    }\n\n    // Bootstrap components\n    .navbar {\n      display: none;\n    }\n    .badge {\n      border: $border-width solid $black;\n    }\n\n    .table {\n      border-collapse: collapse !important;\n\n      td,\n      th {\n        background-color: $white !important;\n      }\n    }\n\n    .table-bordered {\n      th,\n      td {\n        border: 1px solid $gray-300 !important;\n      }\n    }\n\n    .table-dark {\n      color: inherit;\n\n      th,\n      td,\n      thead th,\n      tbody + tbody {\n        border-color: $table-border-color;\n      }\n    }\n\n    .table .thead-dark th {\n      color: inherit;\n      border-color: $table-border-color;\n    }\n\n    // Bootstrap specific changes end\n  }\n}\n","/* Slider */\n\n.slick-slider {\n    position: relative;\n    display: block;\n    box-sizing: border-box;\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    -ms-touch-action: pan-y;\n    touch-action: pan-y;\n    -webkit-tap-highlight-color: transparent;\n}\n.slick-list {\n    position: relative;\n    overflow: hidden;\n    display: block;\n    margin: 0;\n    padding: 0;\n\n    &:focus {\n        outline: none;\n    }\n\n    &.dragging {\n        cursor: pointer;\n        cursor: hand;\n    }\n}\n.slick-slider .slick-track,\n.slick-slider .slick-list {\n    -webkit-transform: translate3d(0, 0, 0);\n    -moz-transform: translate3d(0, 0, 0);\n    -ms-transform: translate3d(0, 0, 0);\n    -o-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n}\n\n.slick-track {\n    position: relative;\n    left: 0;\n    top: 0;\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n\n    &:before,\n    &:after {\n        content: \"\";\n        display: table;\n    }\n\n    &:after {\n        clear: both;\n    }\n\n    .slick-loading & {\n        visibility: hidden;\n    }\n}\n.slick-slide {\n    float: left;\n    height: 100%;\n    min-height: 1px;\n    [dir=\"rtl\"] & {\n        float: right;\n    }\n    img {\n        display: block;\n    }\n    &.slick-loading img {\n        display: none;\n    }\n\n    display: none;\n\n    &.dragging img {\n        pointer-events: none;\n    }\n\n    .slick-initialized & {\n        display: block;\n    }\n\n    .slick-loading & {\n        visibility: hidden;\n    }\n\n    .slick-vertical & {\n        display: block;\n        height: auto;\n        border: 1px solid transparent;\n    }\n}\n.slick-arrow.slick-hidden {\n    display: none;\n}\n","html, body {\n  margin: 0;\n  overflow-x: hidden;\n}\n\nimg{\n  max-width: 100%;\n}\n\nbody{\n  background: $black;\n  color: $white;\n}\n\n.main.jumbotron{\n  background: white;\n  border-radius: 0;\n  margin-bottom: 0;\n  padding: 4rem 0;\n  padding-top: 100px;\n}\n\nmain{\n  .p1{\n    max-width: 660px;\n    margin: auto;\n  }\n  h1{\n    margin-bottom: 27px;\n  }\n}\n\n.hidden-mobile{\n  @include media-breakpoint-down(md) {\n    display: none !important;\n  }\n}\n\n.hidden-desktop{\n  @include media-breakpoint-up(lg) {\n    display: none !important;\n  }\n}\n\n.container-fluid{\n  position: absolute;\n  width: 100%;\n  left: 0;\n  border: 1px solid red;\n}\n\n.separator{\n  margin-top: 85px;\n  margin-bottom: 70px;\n  text-align: center;\n  &:nth-child(2){\n    \n  }\n  img{\n    max-width: 120px;\n  }\n}","@font-face{\n  font-family: 'Alternate-Gothic';\n  src:  url('../fonts/Alternate-Gothic.ttf')\n}\n\n@font-face{\n  font-family: 'ChaparralPro-Bold';\n  src:  url('../fonts/ChaparralPro-Bold.otf')\n}\n\n@font-face{\n  font-family: 'ChaparralPro-BoldIt';\n  src:  url('.